import React, { Component } from 'react';
import { Formik, FormikProps, Form, Field, ErrorMessage } from 'formik';
import {getHospitalSet,updateDepartment, getDepartment} from './../Apis.js'
import { Alert } from 'reactstrap';
import * as Yup from 'yup';
import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';



class UpdateDepartment extends Component {
    constructor(props) {
        super(props);
        this.state = {
           
            visible:false,
            
            showForm:false,
            msgType:'success',
            massege:''
          
        };
        this.onDismiss = this.onDismiss.bind(this);
      }
    onDismiss() {
        this.setState({ visible: false });
      }
    handleSubmit = (values, { 
        props = this.props, 
        setSubmitting 
      }) => {
       
        this.updateDepartment(values,setSubmitting,this.props.match.params.id)
       
    }

    updateDepartment=async(values,setSubmitting,id)=>{
          const responseData= await updateDepartment(values,id);
          console.log(responseData,'response')
          if(responseData.data.message)
          {
              this.setState({ visible:true,massege:'Department Update Failed '+responseData.message,msgType:'danger'})
              setSubmitting(false);
          }
          else{
              this.setState({ visible:true,msgType:'success',massege:'Department Update Successfully'})
              setSubmitting(false);
              return;
          }
      }
      componentDidMount=async()=>{
            const responseData= await getHospitalSet();
            console.log(responseData,'response')
            if(responseData.status == 200){
                this.setState({hospitalSets:responseData.data.result})
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            } 
            const responseDataDepart= await getDepartment(this.props.match.params.id);
            console.log(responseDataDepart,'response')
            if(responseDataDepart.status == 200){
                this.setState({
                
                    name:responseDataDepart.data.name,
                    description:responseDataDepart.data.description,
                    hospital_id:responseDataDepart.data.hospital_id,
                    primary_phone:responseDataDepart.data.primary_phone,
                    created_by:responseDataDepart.data.created_by,
                    showForm:true,
                    
                })
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            } 
     


        }
    render() {
        console.log(this.state.visible)
       
        return(
            <div className="animated fadeIn">
                <Row>
                <Col xs="2">
                </Col>
                    <Col xs="8">
                    <Card>
                        <CardHeader>
                            <strong>Update Department</strong>
                        </CardHeader>
                        <CardBody> 
                            {this.state.showForm&&<Formik
                                    initialValues={{
                                        name: this.state.name,
                                        description: this.state.description,
                                        hospital_id: this.state.hospital_id,
                                        primary_phone: this.state.primary_phone,
                                        modified_by: localStorage.getItem('id'),
                                    }}
                            /*validate={values => {
                                let errors = {};
                                if (!values.email) {
                                  errors.email = 'Required';
                                } else if (
                                  !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                                ) {
                                  errors.email = 'Invalid email address';
                                }
                                return errors;
                              }}*/
                            // validationSchema={Yup.object().shape({
                            // email: Yup
                            //     .string()
                            //     .email()
                            //     .required(),
                            // password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // confirm_password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // phone:Yup
                            //     .number()
                            //     .required(),
                            // alternate_phone:Yup
                            //     .number()
                            //     .required(),
                            // })}
                            
                                    onSubmit={this.handleSubmit}
                                    render={formProps => {
                                        return(
                                        <Form className="form-horizontal">
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Name:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="name" 
                                                    placeholder="Name" 
                                                    id="text-input"
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="first_name" />
                                                </Col>                                        
                                            </FormGroup>
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Description:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="description" 
                                                    placeholder="Description" 
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="middle_name" />
                                                </Col>                                        
                                            </FormGroup>
                                      
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Hospital Name:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                <Field 
                                                    component="select" 
                                                        name="hospital_id" 
                                                        placeholder="Hospital Id" 
                                                        className="form-control"
                                                        >
                                                        {this.state.hospitalSets&&this.state.hospitalSets.map((hospital, index) =>
                                                        <option key={index} value={hospital.id}>{hospital.name}</option>
                                                        )}
                                                        </Field> 
                                                    <ErrorMessage name="short_name" />
                                                </Col>                                        
                                            </FormGroup>
                                      
                                            <FormGroup row>
                                                <Col md="3">                                                           
                                                    <Label htmlFor="text-input">Phone:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="primary_phone" 
                                                    placeholder="Phone" 
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="alternate_phone" />
                                                </Col>                                        
                                            </FormGroup>
                                 
                                            <Col md="3" style={{float:'right'}}>
                                            <button 
                                                type="submit"
                                                className="btn btn-success btn-block custonbtn" 
                                                
                                                disabled={formProps.isSubmitting}>
                                               Update Department
                                            </button>
                                            </Col>
                                            </Form>
                                );
                            }}

                            />}
                             <Alert color={this.state.msgType} style={{marginTop:65}}isOpen={this.state.visible} toggle={this.onDismiss}>
                                {this.state.massege}
                            </Alert>
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </div>
            
        );
    }

}

export default UpdateDepartment;